.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	.breadcrumbs-control.hidden {
	display: none;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	.breadcrumbs-control
	.monaco-breadcrumb-item.selected
	.monaco-icon-label,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	.breadcrumbs-control
	.monaco-breadcrumb-item.focused
	.monaco-icon-label {
	text-decoration-line: underline;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	.breadcrumbs-control
	.monaco-breadcrumb-item.selected
	.hint-more,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	.breadcrumbs-control
	.monaco-breadcrumb-item.focused
	.hint-more {
	text-decoration-line: underline;
}

.monaco-workbench
	.monaco-breadcrumb-item.shows-symbol-icon
	.codicon[class*="codicon-symbol-"] {
	padding-right: 6px;
}

/* breadcrumbs-picker-style */

.monaco-workbench .monaco-breadcrumbs-picker .arrow {
	position: absolute;
	width: 0;
	border-style: solid;
}

.monaco-workbench .monaco-breadcrumbs-picker .picker-item {
	line-height: 22px;
	flex: 1;
}

.monaco-workbench .monaco-breadcrumbs-picker .highlighting-tree {
	height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.monaco-workbench .monaco-breadcrumbs-picker .highlighting-tree > .input {
	padding: 5px 9px;
	position: relative;
	box-sizing: border-box;
	height: 36px;
}

.monaco-workbench .monaco-breadcrumbs-picker .highlighting-tree > .tree {
	height: calc(100% - 36px);
}

.monaco-workbench
	.monaco-breadcrumbs-picker
	.highlighting-tree.inactive
	> .input {
	display: none;
}

.monaco-workbench
	.monaco-breadcrumbs-picker
	.highlighting-tree.inactive
	> .tree {
	height: 100%;
}

.monaco-workbench
	.monaco-breadcrumbs-picker
	.highlighting-tree
	.monaco-highlighted-label
	.highlight {
	font-weight: bold;
}
